<template>
  <v-container style="height: 100%;width:100%;">
    <v-card height="100%" flat>
      <v-toolbar flat>
        <v-toolbar-title>Packages</v-toolbar-title>
      </v-toolbar>
      <v-tabs vertical height="100%" show-arrows>
        <v-tab v-for="d in metadata" :key="d.name" @click="show(d)">
          <v-icon left> mdi-cube </v-icon>
          {{ d.name }}
        </v-tab>

        <v-tab-item v-for="d in metadata" :key="d.name">
          <v-card flat height="100%" v-if="selectedItem">
            <v-card-text>
              <v-card-title class="text-h4">
                {{ d.name }}
              </v-card-title>
              <v-card-subtitle>
                <span>
                  {{ selectedItem.description }}
                </span>
                <br />
                <span> :{{ selectedItem.version }} </span>
              </v-card-subtitle>
              <v-card-text>
                <h3>:</h3>
                <v-card outlined>
                  <v-card-text>
                    <v-icon>mdi-download</v-icon>
                    <a :href="selectedItem.file" target="_blank">{{
                      selectedItem.file
                    }}</a>
                  </v-card-text>
                </v-card>
                <h3>CLI:</h3>

                <v-card outlined>
                  <v-card-text>
                    {{ toCommand(selectedItem.name) }}
                  </v-card-text>
                </v-card>
              </v-card-text>
            </v-card-text>
          </v-card>
        </v-tab-item>
      </v-tabs>
    </v-card>
  </v-container>
</template>

<script>
import axios from "axios";
export default {
  data: () => ({
    metadata: null,
    selectedItem: null,
  }),
  mounted() {
    axios.get("metadata/zh/gc-packages/index.json").then((res) => {
      this.metadata = res.data;
      console.log(res.data);
      this.show(res.data[0]);

    });
  },
  methods: {
    show(d) {
      this.selectedItem = d;
    },
    toGitHub(g) {
      return `https://github.com/${g}`;
    },
    toCommand(n) {
      return `gc-cli addpkg ${n}`;
    },
  },
};
</script>

