<template>
  <v-container>
    <v-card height="100%" flat>
      <v-toolbar flat>
        <v-toolbar-title>Plugins</v-toolbar-title>
      </v-toolbar>
      <v-tabs vertical height="100%" show-arrows>
        <v-tab v-for="d in metadata" :key="d.name" @click="show(d)">
          <v-icon left> mdi-puzzle </v-icon>
          {{ d.name }}
        </v-tab>

        <v-tab-item v-for="d in metadata" :key="d.name">
          <v-card flat height="100%" v-if="selectedItem">
            <v-card-text>
              <v-card-title class="text-h4">
                {{ d.name }}
              </v-card-title>
              <v-card-subtitle>
                <v-chip
                  outlined
                  color="primary"
                  small
                  v-for="t in selectedItem.tags"
                  :key="t"
                >
                  {{ t }}
                </v-chip>
              </v-card-subtitle>
              <v-card-text>
                <h3>:</h3>
                <v-card outlined>
                  <v-card-text>
              <v-icon>mdi-github</v-icon>
              <a :href="toGitHub(selectedItem.github)" target="_blank">{{
                selectedItem.github
              }}</a>
                  </v-card-text>
                </v-card>
                <h3>CLI:</h3>

                <v-card outlined>
                  <v-card-text>
                    {{ toCommand(selectedItem.name) }}
                  </v-card-text>
                </v-card>

                <h3>README:</h3>

                <v-card outlined>
                  <v-card-text>
                    <div v-html="readme" />
                  </v-card-text>
                </v-card>
              </v-card-text>
            </v-card-text>
          </v-card>
        </v-tab-item>
      </v-tabs>
    </v-card>
  </v-container>
</template>

<script>
import axios from "axios";
import { marked } from "marked";
export default {
  data: () => ({
    metadata: null,
    selectedItem: null,
    readme: "",
  }),
  mounted() {
    axios.get("metadata/zh/gc-plugin/index.json").then((res) => {
      this.metadata = res.data;
      console.log(res.data);
      this.show(res.data[0]);
    });
    
  },
  methods: {
    show(d) {
      this.selectedItem = d;
      this.readme = "Loading";
      //https://raw.githubusercontent.com/gc-mojoconsole/gc-mojoconsole-backend/main/README.md

      axios
        .get(
          `https://raw.githubusercontent.com/${this.selectedItem.github}/${this.selectedItem.branch}/README.md`
        )
        .then((res) => {
          console.log(res.data);

          this.readme = marked.parse(res.data);
        });
    },
    toGitHub(g) {
      return `https://github.com/${g}`;
    },
    toCommand(n) {
      return `gc-cli addplu ${n}`;
    },
  },
};
</script>

<style>
.v-tab{
  justify-content: flex-start !important;
}
</style>